.outer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 156px;
    flex-direction: column;
}

.container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0 75px;
}

.poster {
    width: 253px;
    height: auto;
    object-fit: cover;
    margin-right: 72px;
    border-radius: 20px;
}

.title-name-description {
    flex-grow: 1;
}

.name{
    font-family: 'Readex Pro', sans-serif;
    color: #FFFFFF;
    max-width: 820px;
    font-size: 36px;
    margin-bottom: 5px;
    margin-top: 10px;
}

.name-spec{
    font-family: 'Readex Pro', sans-serif;
    color: #FFFFFF;
}

.description{
    margin-top: 0;
    font-family: 'Readex Pro', sans-serif;
    color: rgba(255, 255, 255, 0.5);
    max-width: 820px;
    font-size: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
}

.ppp{
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
    font-size: 18px;
}

.anime-specifications {
    margin-top: 20px;
    max-width: 253px;

}

.player {
    margin-top: 20px;
    max-width: 820px;
}


