.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 200px 20px 0;
}

.title-name-specifications {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 20px;
}

.poster {
    max-width: 150px;
    height: auto;
    object-fit: cover;
    margin-right: 20px;
    border-radius: 20px;
}


.name {
    font-family: 'Readex Pro', sans-serif;
    color: #FFFFFF;
    font-size: 24px;
    margin-bottom: 10px;
    margin-top: 0;
}

.name-spec{
    font-family: 'Readex Pro', sans-serif;
    color: #FFFFFF;
}

.anime-specifications {
    flex-grow: 1;
}

.ppp {
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
    font-size: 14px;
}

.player {
    width: 100%;
}

.description {
    font-family: 'Readex Pro', sans-serif;
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 18px;
    text-align: justify;
    margin-top: 20px;
}
